<template>
  <div class="newsline">
    <div class="news-toolbar">
      <button type="button" class="news-button" @click="prev">
        <svg style="width: 24px; height: 24px" viewBox="0 0 24 24">
          <path
            fill="currentColor"
            d="M15,18H13.5L7.5,12L13.5,6H15V18M10.33,12L13,14.67V9.33L10.33,12Z"
          />
        </svg>
      </button>
      <button class="news-button">
        {{ selected + 1 }} из {{ news.length }}
      </button>

      <button type="button" class="news-button" @click="next">
        <svg style="width: 24px; height: 24px" viewBox="0 0 24 24">
          <path
            fill="currentColor"
            d="M9,6H10.5L16.5,12L10.5,18H9V6M13.67,12L11,9.33V14.67L13.67,12Z"
          />
        </svg>
      </button>

      <button
        class="news-button menu"
        :class="{ dropdown }"
        type="button"
        @click.stop="toggleDropdown"
      >
        <span class="selected" v-html="news[selected].NAME" />
        <span class="chevron">
          <svg style="width: 24px; height: 24px" viewBox="0 0 24 24">
            <path
              fill="currentColor"
              d="M8.59,16.58L13.17,12L8.59,7.41L10,6L16,12L10,18L8.59,16.58Z"
            />
          </svg>
        </span>

        <ul
          class="news-dropdown-menu"
          ref="dropdown"
          :class="{ active: dropdown }"
        >
          <li
            v-for="(item, index) in news"
            @click="goto(index)"
            class="dropdown-item"
            :key="index"
          >
            <span v-html="item.NAME" />
          </li>
        </ul>
      </button>
    </div>
    <transition :name="transition" mode="out-in">
      <div class="body" :key="selected" v-html="news[selected].BODY" />
    </transition>
  </div>
</template>
<script>
import { newsline, news } from "../news.json";

export default {
  name: "Newsline",
  data() {
    return {
      selected: 0,
      transition: "slide-right",
      dropdown: false,
      tinySliderOptions: {
        mouseDrag: true,
        loop: false,
        items: 1,
        gutter: 20,
        swipeAngle: 45,
        controls: false,
        nav: false,
      },
    };
  },

  computed: {
    news() {
      return news;
    },
    newsline() {
      return newsline;
    },
  },
  mounted() {
    document.addEventListener("click", this.removeDropdown);
  },
  beforeDestroy() {
    document.removeEventListener("click", this.removeDropdown);
  },
  methods: {
    goto(index) {
      this.transition = this.selected > index ? "slide-left" : "slide-right";
      if (index > this.news.length - 1) {
        index = this.news.length - 1;
      }
      if (index < 0) {
        index = 0;
      }
      this.selected = index;
    },
    next() {
      this.goto(this.selected + 1);
    },
    prev() {
      this.goto(this.selected - 1);
    },
    toggleDropdown() {
      this.dropdown = !this.dropdown;
    },
    removeDropdown() {
      this.dropdown = false;
    },
  },
};
</script>
